*{
    box-sizing: border-box;
}
body {
    margin:0;
    font-size: 1.4545454545454546vw;
   }


   
   /* Max Font Size */
   @media screen and (min-width:1100px) {
       body {font-size: 1.1818181818181819rem;}
    }
    
    /* Min Font Size */
    @media screen and (max-width:700px) {
        body {font-size: 0.7363636363636364rem;}
   
   }

h1,h2,h3,h4,h5,h6{
    margin:0;
    color:#0c0d0c;
}

a{
    text-decoration:none;
    color:#fff;
    cursor:pointer;
}
.section{
    padding:4em 4em;
    min-height: 100vh;
}





/* SWEET ALERT */
.my-actions { margin: 2em 2em 0; }
.order-1 { order: 1; }
.order-2 { order: 2; }
.order-3 { order: 3; }

.right-gap {
  margin-right: auto;
}